<template>
    <div class="filtered-products">
      <div class="loader-box" v-if="isLoading">
        <div class="reverse-spinner"></div>
      </div>
      <div v-else-if="totalProducts === 0">NO Products Found</div>
      <div v-else>
        
        <ProductsSection :products-list="productList" />
      </div>
    </div>
  </template>
  
  <script setup>
  import { computed, onMounted } from "vue";
  import { useStore } from "vuex";
  import ProductsSection from "@/esf_thehague_schinsleder/core/components/ProductsSection";
  // import chunkArray from "@/base/helpers/chunkArray";
  
  const store = useStore();
  
  const isLoading = computed(() => store.getters["category/getLoadProducts"]);
  const productList = computed(() => store.getters["category/getShowProducts"]);
  const totalProducts = computed(
    () => store.getters["category/getTotalProducts"]
  );
  // const productsChunks = computed(() => chunkArray(store.getters["category/getShowProducts"], 12));
  
  const isAlive = () => {
    setTimeout(() => {
      if (isLoading.value) {
        window.location.reload();
      }
    }, 3000);
  };
  
  onMounted(() => {
    isAlive();
  });
  </script>
  