<template>
  <div class="content-filter">
    <div class="cst-pagination-bar">
      <label class="pagination-bar-title">
        <span class="pagination-text"> Pagina </span>
        <span class="fw-bold"> {{ currentPage }} </span>
        <span class="pagination-text"> van </span>
        <span class="fw-bold"> {{ Math.ceil(totalProds / prodsPerPage) }} </span>
      </label>
      <div class="d-flex align-items-center">
        <span class="pagination-btn">Pagina:</span>
        <BPagination
          :total-rows="totalProds"
          :per-page="prodsPerPage"
          :model-value="currentPage"
          @update:model-value="changePage"
          class="cst-pagination align-items-center mb-0"
          aria-controls="cat-item"
          limit="3"
          hide-goto-end-buttons="true"
        >
          <BLink class="page-link" :to="goToPage(page)">
            {{ page }}
          </BLink>
        </BPagination>
      </div>
    </div>
    <div class="pagination-bar-down">
      <div class="left">
        <div class="display-drop">
          <div class="dropdown-category d-flex">
            <BFormSelect
              v-model="selectedCategory"
              :options="menuItems"
              class="rounded-2 w-25"
              value-field="id"
              text-field="name"
            />

            <BFormSelect
              v-if="hasSubCategories"
              v-model="selectedSubCategories"
              :options="subCategoriesOptions"
              class="mx-5 mx-md-10 w-25 rounded-2"
              value-field="id"
              text-field="name"
            />

            <BFormSelect
              v-if="hasChildCategories"
              v-model="selectedChildCategory"
              :options="childCategoriesOptions"
              class="w-25 rounded-2"
              value-field="id"
              text-field="name"
            />
          </div>
        </div>
      </div>
    </div>
    <p class="fw-bold article-para mt-15">
      {{ $t("articles_match", { totalproduct: totalProds }) }}
    </p>
    <p class="article-para">{{ $t("search_function") }}</p>
  </div>
</template>
<script setup>
import { computed, onMounted, ref, watch } from "vue";
import { useStore } from "vuex";
import { useRoute, useRouter } from "vue-router";
import { useI18n } from "vue-i18n";
import config from "@config";
import ProductFilter from "@/esf_thehague_schinsleder/core/components/ProductFilter.vue";

const store = useStore();
const route = useRoute();
const router = useRouter();
const { t } = useI18n();

let selectedCategory = ref([]);
let subCategoriesOptions = ref([]);
let childCategoriesOptions = ref([]);
let hasSubCategories = ref(false);
let hasChildCategories = ref(false);
let selectedSubCategories = ref();
const selectedChildCategory = ref(null);

const sorting = computed(() => store.getters["category/getSorting"]);
const currentPage = computed(() => store.getters["category/getCurrentPage"]);
const totalProds = computed(() => store.getters["category/getTotalProducts"]);
const prodsPerPage = computed(() => store.getters["category/getProductsPerPage"]);
const totalPages = computed(() => store.getters["category/getTotalPages"]);
const category = computed(() => store.getters["category/getCurrentCategory"]);

const menuItems = computed(() => store.getters["sidebar/getSidebarCategoryItems"]);

watch(selectedCategory, () => {
  menuItems.value.forEach((category) => {
    if (category.id == selectedCategory.value) {
      hasSubCategories.value = parseInt(category.children_count) > 0;
      if (hasSubCategories) {
        subCategoriesOptions.value = category.children;
      }
      hasChildCategories.value = parseInt(category.children_count) > 0;
      if (hasChildCategories) {
        hasChildCategories.value = false;
      }
      router.push(`/${category.url_path}`);
    }
  });
});

watch(selectedSubCategories, () => {
  subCategoriesOptions.value.forEach((category) => {
    if (category.id == selectedSubCategories.value) {
      hasChildCategories.value = parseInt(category.children_count) > 0;
      if (hasChildCategories) {
        childCategoriesOptions.value = category.children;
      }
      router.push(`/${category.url_path}`);
    }
  });
});

watch(selectedChildCategory, () => {
  childCategoriesOptions.value.forEach((category) => {
    if (category.id == selectedChildCategory.value) {
      router.push(`/${category.url_path}`);
    }
  });
});

const changePage = (pageNo) => {
  router.currentRoute.value.meta = "top";
  store.commit("category/setCurrentPage", { pageNo, store });

  const { path } = route;
  const query = { ...route.query };

  if (pageNo != 1) query["page"] = pageNo;
  else delete query["page"];

  router.replace({ path, query });
};

const goToPage = (page) => {
  const { path } = route;
  const query = { ...route.query };

  if (page != 1) query["page"] = page;
  else delete query["page"];

  return { path, query };
};

onMounted(() => {
  const { page } = route.query;
  const path = "/" + category.value.url_path;
  const query = { ...route.query };
  if (page && totalPages.value < parseInt(page)) {
    delete query["page"];
  }
  const pageSize = config.products?.perPage;
  const perPage = store.getters["category/getProductsPerPage"];
  if (perPage != pageSize) query["page-size"] = perPage;

  router.replace({ path, query });

  let converArray = category.value.path.split("/");

  if (converArray[3]) {
    selectedCategory.value = converArray[3];
  }
  if (converArray[4]) {
    selectedSubCategories.value = converArray[4];
  }
  if (converArray[5]) {
    selectedChildCategory.value = converArray[5];
  }
});
</script>
